@font-face {
  font-family: "cormorant";
  src: "../fonts/cormorant-bold.woff2", "../fonts/cormorant-bold.woff";
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "cormorant";
  src: "../fonts/cormorant-light.woff2", "../fonts/cormorant-light.woff";
  font-weight: 300;
  font-style: normal; }

@font-face {
