  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: "opensans";
  src: "../fonts/opensans-light.woff2", "../fonts/opensans-light.woff";
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: "opensans";
  src: "../fonts/opensans-regular.woff2", "../fonts/opensans-regular.woff";
  font-weight: 400;
  font-style: normal; }

.container {
  width: 320px;
  margin: 0 auto; }

.main-menu {
  z-index: 10;
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  top: 0;
  width: 100%;
  padding: 100px 15px 56px 38px;
  background-color: #f1ede4;
  background-image: url("../img/menu-bg-mobile.jpg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover; }

.main-menu__close-button {
  position: absolute;
  top: 20px;
  right: 15px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none; }

.main-menu__label {
  width: 100%;
  margin: 0;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 600; }

.main-menu__list {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 400;
  color: #342b2b;
  text-decoration: none;
  list-style: none; }
  .main-menu__list--form {
    display: none; }

.main-menu__item {
  margin-bottom: 10px; }
  .main-menu__item--mobile-hidden {
    display: none; }

.main-menu__link {
  font-size: 16px;
  font-weight: 400;
  color: #342b2b;
  text-decoration: none; }
  .main-menu__link--semibold {
    font-weight: 600; }
  .main-menu__link--medium {
    font-size: 18px;
    font-weight: 500; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-family: "cormorant", "Verdana", sans-serif;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 400;
  font-style: normal;
  color: #342b2b;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

a {
  color: #79100b; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none; }

.short-menu {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none; }

.short-menu__item {
  margin-left: 30px; }

.header {
  position: absolute;
  left: 50%;
  margin-left: -160px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 9px 0 28px; }

.header__menu-button {
  margin-left: 15px;
  padding: 0;
  background-color: transparent;
  border: none; }

.header__logo {
  display: none; }

.header__icons {
  margin-right: 15px; }

.promo {
  background-color: #f1ede4;
  background-image: url("../img/slide-1-mobile.jpg");
  background-repeat: no-repeat;
  background-position: -165px;
  background-size: cover; }

.promo__wrap {
  display: flex;
  padding: 50px 15px;
  background-color: rgba(241, 237, 228, 0.8); }

.promo__sale-menu,
.promo__side-nav,
.promo__social {
  display: none; }

.promo__label-wrap {
  z-index: 1;
  flex-shrink: 0;
  width: 135px;
  margin-top: 140px; }

.promo__label {
  font-size: 24px;
  font-weight: 500; }

.promo__link {
  padding-bottom: 5px;
  text-decoration: none;
  border-bottom: 1px solid #79100b; }

.promo__slider {
  margin-left: -64px; }

.slider__list {
  margin: 0;
  padding: 0; }

.slider__item {
  display: none; }
  .slider__item--active {
    display: block; }

.slider__name,
.slider__price,
.slider__control,
.slider__link {
  display: none; }
